.nav{position: fixed; top: 50px; left: 10px; z-index: 1; display: flex; flex-direction: column; background: white; padding: 10px 20px; border-radius: 5px; box-shadow: 0 13px 27px -5px rgba(50,50,93,.25), 0 8px 16px -8px rgba(0,0,0,.3);}

@media screen and (max-width: 1090px) {
    .nav{left: initial; top: initial; bottom: 0; width: 100%;}
}

.nav a{text-decoration: none; color: #acb5c3; text-transform: uppercase; font-size: 12px; text-align: center; font-weight: 600;}
.nav a:not(:last-child){margin-bottom: 20px;}